import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  heroHighlight(data) {
    const response = Api().post(apiPath.herohighlights, data, {
      headers: {
        "Content-Type": "multipart/form-data"
      }
    });
    return response;
  },

  getVisitorsAndViews() {
    const response = Api().get(
      apiPath.analytics + "/total-visitors-and-pageviews"
    );
    return response;
  }
};
