import HighlightIndex from "@/views/Highlight/index";
import HighlightCreate from "@/views/Highlight/create";
import HighlightShow from "@/views/Highlight/show";
import HighlightAddItem from "@/views/Highlight/addItem";

export default [
  {
    path: "highlight",
    name: "Highlight",
    component: HighlightIndex,
    meta: { noGuard: true },
  },
  {
    path: "highlight/create",
    name: "HighlightCreate",
    component: HighlightCreate,
    meta: { noGuard: true },
  },
  {
    path: "highlight/:id",
    name: "HighlightShow",
    component: HighlightShow,
    meta: { noGuard: true },
  },
  {
    path: "highlight/:id/add",
    name: "HighlightAddItem",
    component: HighlightAddItem,
    meta: { noGuard: true },
  },
];
