<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <strong class="card-title">Hosts</strong>
          <router-link :to="{name: 'HostCreate'}">
            <button type="button" class="btn btn-primary px-4">
              <span class="h4 font-weight-bold">+</span>
            </button>
          </router-link>
        </div>
        <div class="card-body">
          <div v-if="hosts == 'loading'" class="text-center">
            <div class="spinner-border text-primary"></div>
          </div>
          <table v-else-if="hosts != null" class="table">
            <tbody>
              <tr v-for="(host, index) in hosts" :key="host.id">
                <th scope="row">{{ index+1 }}</th>
                <td>{{ host.name }}</td>
                <td>
                  <router-link :to="{name: 'HostEdit', params: {id: host.id}}">
                    <div class="btn btn-warning btn-sm mx-3">
                      <i class="fas fa-edit"></i>
                    </div>
                  </router-link>

                  <div @click="deleteHost(host)" class="btn btn-danger btn-sm mx-3">
                    <i class="fas fa-trash"></i>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HostService from "@/services/HostService";

export default {
  data() {
    return {
      hosts: "loading"
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Hosts");
    this.getHosts();
  },

  methods: {
    getHosts() {
      HostService.index().then(res => {
        this.hosts = res;
      });
    },

    deleteHost(item) {
      HostService.delete(item.id).then(res => {
        let index = this.hosts.indexOf(item);
        this.hosts.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    }
  }
};
</script>
