import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  index() {
    const response = Api().get(apiPath.trendings);
    return response;
  },

  sort(data) {
    const response = Api().post(apiPath.trendings + "/sort", data);
    return response;
  },

  store(data) {
    const response = Api().post(apiPath.trendings, data);
    return response;
  },

  delete(id) {
    const response = Api().delete(apiPath.trendings + "/" + id);
    return response;
  }
};
