import FranchiseIndex from "@/views/Franchise/index";
import FranchiseEdit from "@/views/Franchise/edit";
import FranchiseCreate from "@/views/Franchise/create";

export default [
  {
    path: "franchise",
    name: "Franchise",
    component: FranchiseIndex,
    meta: { noGuard: true },
  },
  {
    path: "franchise/create",
    name: "FranchiseCreate",
    component: FranchiseCreate,
    meta: { noGuard: true },
  },
  {
    path: "franchise/:id",
    name: "FranchiseEdit",
    component: FranchiseEdit,
    meta: { noGuard: true },
  },
];
