<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Highlight'}">
            <div class="btn btn-link">
              <div class="i fas fa-arrow-left"></div>
            </div>
          </router-link>
          <strong class="card-title">Create highlight</strong>
        </div>

        <div class="card-body">
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label class="control-label mb-1">Name</label>
              <input v-model="name" type="text" class="form-control">
            </div>

            <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HighlightService from "@/services/HighlightService";

export default {
  data() {
    return {
      name: ""
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Create highlight");
  },

  methods: {
    onSubmit() {
      HighlightService.store({
        name: this.name
      }).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "Highlight" });
      });
    }
  }
};
</script>
