import axios from "axios";
import Cookie from "js-cookie";
import { logout } from "@/helpers/auth";
import Vue from "vue";

export default () => {
  let apiHeaders = {
    "Content-Type": "application/json"
  };

  let token = Cookie.get("auth_token");
  if (token) {
    apiHeaders["Authorization"] = "Bearer " + token;
  }

  let apiRequest = axios.create({
    headers: apiHeaders
  });

  apiRequest.interceptors.response.use(
    response => {
      switch (response.data.status) {
        case "success":
          return response.data.body;
        default:
          Vue.prototype.$snotify.error(response.data.message);
          throw new axios.Cancel(response.data.message);
      }
    },
    function(error) {
      if (error.response.status === 401) {
        logout();
        return null;
      } else {
        console.log(error);
        Vue.prototype.$snotify.error(error.message);
        throw new axios.Cancel(error.message);
      }
    }
  );

  return apiRequest;
};

export const OtherApi = function() {
  let apiHeaders = {
    "Content-Type": "application/json"
  };

  let apiRequest = axios.create({
    headers: apiHeaders
  });

  return apiRequest;
};
