import Cookie from "js-cookie";
import { store } from "@/store";

const keywords = {
  superadmin: "superadmin",
  admin: "admin",
  member: "member",
  normal_user: "normal_user"
};

const isAuth = function() {
  let token = Cookie.get("auth_token");
  if (!token) return false;

  let authUser = store.state.authUser;
  return authUser != null ? true : false;
};

const getUserRoles = function() {
  let token = Cookie.get("auth_token");
  if (!token || store.state.authUser == null) {
    return null;
  }
  return store.state.authUser.roles;
};

const getKeywordSet = function(role) {
  switch (role) {
    case "member":
      return [keywords.member, keywords.admin, keywords.superadmin];
    case "admin":
      return [keywords.admin, keywords.superadmin];
    case "superadmin":
      return [keywords.superadmin];
    default:
      return null;
  }
};

const roleCheck = function(role, roles) {
  let flag = false;
  let userRoles = roles ? roles : getUserRoles();
  let keywordSet = getKeywordSet(role);

  if (userRoles && keywordSet) {
    userRoles.forEach(role => {
      if (keywordSet.includes(role.keyword)) {
        flag = true;
      }
    });
    return flag;
  }
};

export default {
  methods: {
    isAuth,
    roleCheck
  }
};
