<template>
  <div v-if="isDocReady" class="row">
    <div class="col-sm-12 col-lg-6 mx-auto">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Franchise'}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Edit franchise</strong>
        </div>
        <div class="card-body">
          <div class="form-group">
            <label class="control-label mb-1">Name</label>
            <input type="text" v-model="franchise.name" class="form-control">
          </div>

          <div class="form-group">
            <label class="control-label mb-1">Genres</label>
            <multiselect
              v-model="franchise.genres"
              :options="genres"
              :multiple="true"
              selectLabel="Enter дарж сонгох"
              label="name"
              value="id"
              trackBy="id"
            ></multiselect>
          </div>

          <button @click="submitFranchise()" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>

    <div class="col-sm-12 col-lg-6 mx-auto">
      <div class="card">
        <div class="card-header">
          <strong class="card-title">Nodes</strong>
        </div>
        <div class="card-body">
          <table class="table">
            <tbody>
              <draggable :list="franchise.nodes" class="dragArea">
                <tr v-for="(node, index) in franchise.nodes" :key="node.id">
                  <td>
                    <span style="vertical-align: -webkit-baseline-middle;">{{ index+1 }}</span>
                  </td>
                  <td>
                    <input type="text" v-model="node.label" class="form-control">
                  </td>
                  <td>
                    <strong style="vertical-align: -webkit-baseline-middle;">
                      <span>{{ node.title.name }}</span>
                    </strong>
                  </td>
                </tr>
              </draggable>
            </tbody>
          </table>

          <button @click="submitNodes()" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FranchiseService from "@/services/FranchiseService";
import GenreService from "@/services/GenreService";

import Multiselect from "vue-multiselect";
import draggable from "vuedraggable";

export default {
  components: { Multiselect, draggable },

  data() {
    return {
      franchise: null,
      genres: null
    };
  },

  computed: {
    isDocReady() {
      return this.franchise && this.genres;
    }
  },

  created() {
    this.$store.dispatch("setPageTitle", "Edit franchise");
    this.getFranchise();
    this.getGenres();
  },

  methods: {
    getFranchise() {
      FranchiseService.fetch(this.$route.params.id).then(res => {
        this.franchise = res;
      });
    },

    getGenres() {
      GenreService.getGenres().then(res => {
        this.genres = res;
      });
    },

    submitFranchise() {
      let genre_ids = this.franchise.genres.map(genre => genre.id);
      let data = {
        name: this.franchise.name,
        genre_ids: genre_ids
      };
      FranchiseService.update(this.$route.params.id, data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    submitNodes() {
      let nodesObj = [];
      for (var i = 0; i < this.franchise.nodes.length; i++) {
        nodesObj.push({
          id: this.franchise.nodes[i].id,
          position: i + 1,
          label: this.franchise.nodes[i].label
        });
      }

      FranchiseService.updateNodes({
        nodesObj: nodesObj
      }).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
      });
    }
  }
};
</script>
