<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Event'}" class="btn btn-link">
            <i class="fas fa-arrow-left"></i>
          </router-link>
          <strong class="card-title">Create event</strong>
        </div>

        <div class="card-body">
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label class="control-label mb-1">Name</label>
              <input v-model="name" type="text" class="form-control">
            </div>
            <div class="row form-group">
              <div class="col col-md-4">
                <label class="control-label mb-1">Thumbnail image</label>
              </div>
              <div class="col-12 col-md-8">
                <input type="file" id="thumbnail" accept="image/*">
              </div>
            </div>
            <div class="form-group">
              <label class="control-label mb-1">URL</label>
              <input v-model="fb_url" type="text" class="form-control">
            </div>

            <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EventService from "@/services/EventService";

export default {
  data() {
    return {
      name: "",
      fb_url: ""
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Create event");
  },

  methods: {
    onSubmit() {
      let data = this.createFormData();
      EventService.store(data).then(res => {
        this.$router.push({ name: "Event" });
      });
    },

    createFormData() {
      const data = new FormData();
      data.append("name", this.name);
      data.append("fb_url", this.fb_url);
      data.append("thumbnail", document.getElementById("thumbnail").files[0]);
      return data;
    }
  }
};
</script>
