import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  index(options) {
    const response = Api().get(apiPath.members, options);
    return response;
  },

  fetch(id) {
    const response = Api().get(apiPath.members + "/" + id);
    return response;
  },

  create(data) {
    const response = Api().post(apiPath.members, data);
    return response;
  },

  update(id, data) {
    const response = Api().patch(apiPath.members + "/" + id, data);
    return response;
  },

  delete(id) {
    const response = Api().delete(apiPath.members + "/" + id);
    return response;
  }
};
