<template>
  <div class="row">
    <div class="col-12">
      <div v-if="member" class="card">
        <div class="card-header">
          <router-link :to="{name: 'Member'}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Edit member</strong>
        </div>
        <div class="card-body">
          <form @submit.prevent="onSubmit">
            <div class="card-body">
              <div class="form-group">
                <label class="control-label mb-1">Name</label>
                <input v-model="member.name" type="text" class="form-control">
              </div>
              <div class="form-group">
                <label class="control-label mb-1">Status</label>
                <select v-model="member.status" class="form-control">
                  <option v-for="status in statuses" :key="status">{{ status }}</option>
                </select>
              </div>
            </div>

            <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MemberService from "@/services/MemberService";

export default {
  data() {
    return {
      member: null,
      statuses: ["active", "inactive"]
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Edit member");
    this.getMember();
  },

  methods: {
    getMember() {
      MemberService.fetch(this.$route.params.id).then(
        res => (this.member = res)
      );
    },

    onSubmit() {
      MemberService.update(this.member.id, this.member).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "Member" });
      });
    }
  }
};
</script>
