<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <strong class="card-title">Main highlight</strong>
        </div>
        <div class="card-body">
          <div class="row">
            <div class="col-sm-12 col-lg-4">
              <form>
                <div class="form-group">
                  <div class="input-group">
                    <div class="input-group-addon">
                      <i class="fa fa-search"></i>
                    </div>
                    <input
                      v-model="searchQuery"
                      placeholder="Search title"
                      type="text"
                      class="form-control"
                      v-on:input="searchDebounce"
                    >
                  </div>
                </div>
              </form>
              <div v-if="titles == 'loading'" class="text-center">
                <div class="spinner-border text-primary"></div>
              </div>
              <table v-else class="table">
                <tbody>
                  <tr v-for="title in titles" :key="title.id">
                    <td>{{ title.name }}</td>
                    <td>
                      <div
                        @click="selectTitle(title.id)"
                        :class="[title.id == title_id ? 'btn-success' : 'btn-primary', 'btn btn-sm']"
                      >Select</div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="col-sm-12 col-lg-8">
              <div class="form-group">
                <textarea v-model="subtext" placeholder="Sub text" class="form-control" rows="3"></textarea>
              </div>
              <div class="row form-group">
                <div class="col col-md-3">
                  <label>Cover</label>
                </div>
                <div class="col-12 col-md-9">
                  <input type="file" id="cover" accept="image/*">
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3">
                  <label>Logo</label>
                </div>
                <div class="col-12 col-md-9">
                  <input type="file" id="logo" accept="image/*">
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3">
                  <label>Top position</label>
                </div>
                <div class="col-12 col-md-9">
                  <input v-model="top_position" type="text" class="form-control">
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3">
                  <label>Left position</label>
                </div>
                <div class="col-12 col-md-9">
                  <input v-model="left_position" type="text" class="form-control">
                </div>
              </div>
            </div>
          </div>

          <button @click="onSubmit" type="button" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import TitleService from "@/services/TitleService";
import OtherService from "@/services/OtherService";

export default {
  data() {
    return {
      searchQuery: "",
      titles: [],
      title_id: null,
      subtext: null,
      top_position: "40%",
      left_position: "10%"
    };
  },

  methods: {
    searchDebounce: _.debounce(function(e) {
      let queryParams =
        this.searchQuery != "" ? { name: this.searchQuery } : null;
      this.titles = "loading";
      this.searchTitle(queryParams);
    }, 500),

    searchTitle(queryParams) {
      TitleService.index({
        params: queryParams
      }).then(res => {
        this.titles = res;
      });
    },

    selectTitle(id) {
      this.title_id = id;
    },

    onSubmit() {
      const data = this.createFormData();
      OtherService.heroHighlight(data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "Settings" });
      });
    },

    createFormData() {
      const data = new FormData();
      data.append("title_id", this.title_id);
      data.append("subtext", this.subtext);
      data.append("cover", document.getElementById("cover").files[0]);

      if (
        document.getElementById("logo") &&
        document.getElementById("logo").files[0]
      ) {
        data.append("logo", document.getElementById("logo").files[0]);
      }

      data.append("top_position", this.top_position);
      data.append("left_position", this.left_position);
      return data;
    }
  }
};
</script>
