<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div
          class="card-header d-flex justify-content-between align-items-center"
        >
          <strong class="card-title">{{ title.name }}</strong>
        </div>

        <div class="card-body">
          <div class="d-flex flex-wrap">
            <div
              v-for="episode in episodes"
              :key="episode.id"
              class="col-sm-6 col-lg-4 col-xl-3"
            >
              <div class="card">
                <div class="card-header d-flex justify-content-between">
                  <router-link
                    :to="{
                      name: 'EpisodeEdit',
                      params: {
                        titleId: $route.params.id,
                        episodeId: episode.id,
                      },
                    }"
                  >
                    <div class="d-flex align-items-center">
                      <span style="color: black;">
                        <strong class="card-title mr-4">{{
                          episode.number
                        }}</strong>
                      </span>
                      <span style="color: black;">{{
                        convertDate(episode.created_at)
                      }}</span>
                    </div>
                  </router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TitleService from "@/services/TitleService";
import EpisodeService from "@/services/EpisodeService";

export default {
  data() {
    return {
      title: null,
      episodes: null,
    };
  },

  computed: {
    isDocReady() {
      return this.title && this.episodes ? true : false;
    },
  },

  async mounted() {
    this.$store.dispatch("setPageTitle", "Episode list");
    await this.getTitle();
    await this.getEpisode();
    document.dispatchEvent(new Event("x-app-rendered"));
  },

  methods: {
    async getTitle() {
      let res = await TitleService.fetch(this.$route.params.id);
      this.title = res;
    },

    async getEpisode() {
      let res = await EpisodeService.index(this.$route.params.id);
      this.episodes = res;
    },
  },
};
</script>
