import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  index() {
    const response = Api().get(apiPath.highlights);
    return response;
  },

  fetch(id) {
    const response = Api().get(apiPath.highlights + "/" + id);
    return response;
  },

  store(data) {
    const response = Api().post(apiPath.highlights, data);
    return response;
  },

  sort(data) {
    const response = Api().post(apiPath.highlights + "/sort", data);
    return response;
  },

  delete(id) {
    const response = Api().delete(apiPath.highlights + "/" + id);
    return response;
  },

  items_sort(data) {
    const response = Api().post(apiPath.highlight_items + "/sort", data);
    return response;
  },

  items_store(data) {
    const response = Api().post(apiPath.highlight_items, data);
    return response;
  },

  items_delete(id) {
    const response = Api().delete(apiPath.highlight_items + "/" + id);
    return response;
  }
};
