import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  search(options) {
    const response = Api().get(apiPath.users, options);
    return response;
  },

  getUser(options) {
    const response = Api().get(apiPath.users + "/show", options);
    return response;
  },

  getRoles() {
    const response = Api().get(apiPath.roles);
    return response;
  },

  postRoles(data) {
    const response = Api().post(apiPath.roles, data);
    return response;
  }
};
