<script>
import AuthService from "@/services/AuthService";

export default {
  computed: {
    pageTitle() {
      return this.$store.state.pageTitle;
    }
  },

  watch: {
    pageTitle(title) {
      document.title = title;
    }
  },

  created() {
    document.title = this.pageTitle;
  }
};
</script>

