<template>
  <div class="card">
    <div class="card-header">
      <strong>
        <span>Оролцсон гишүүд</span>
      </strong>
    </div>
    <div v-if="members" class="card-body">
      <multiselect
        v-model="selectedMember"
        :options="members"
        selectLabel="Enter дарж сонгох"
        label="name"
        @select="onSelectMember"
        @close="selectedMember = null"
      ></multiselect>

      <br />

      <table class="table">
        <tbody>
          <tr v-for="member in formatContributions" :key="member.id">
            <td style="vertical-align: middle;">
              <strong>{{ member.name }}</strong>
            </td>
            <td>
              <div class="btn-group" role="group" aria-label="Basic example">
                <div
                  :class="
                    contributionBtnStyle(member.contributions, 'translate')
                  "
                  @click="
                    toggleContribution(member.id, member.name, 'translate')
                  "
                >
                  Орчуулга
                </div>
                <div
                  :class="contributionBtnStyle(member.contributions, 'encode')"
                  @click="toggleContribution(member.id, member.name, 'encode')"
                >
                  Энкод
                </div>
                <div
                  :class="contributionBtnStyle(member.contributions, 'review')"
                  @click="toggleContribution(member.id, member.name, 'review')"
                >
                  Хянах
                </div>
                <div
                  :class="
                    contributionBtnStyle(member.contributions, 'participate')
                  "
                  @click="
                    toggleContribution(member.id, member.name, 'participate')
                  "
                >
                  Оролцсон
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";

export default {
  props: ["members", "episode"],
  components: { Multiselect },

  data() {
    return {
      selectedMember: null,
    };
  },

  computed: {
    formatContributions() {
      if (!this.episode) return [];

      let arr = [];
      this.episode.contributions.forEach((contribution) => {
        let memberRow = null;
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].id == contribution.member_id) {
            memberRow = arr[i];
            break;
          }
        }

        if (memberRow != null) {
          memberRow.contributions.push({
            episode_id: contribution.episode_id,
            type: contribution.type,
          });
        } else {
          arr.push({
            id: contribution.member_id,
            name: contribution.member.name,
            contributions: [
              { episode_id: contribution.episode_id, type: contribution.type },
            ],
          });
        }
      });

      return arr;
    },
  },

  methods: {
    pushContribution(member_id, member_name, type, index) {
      let contribution = {
        id: null,
        type: type,
        member_id: member_id,
        episode_id: this.$route.params.episodeId,
        member: {
          name: member_name,
        },
      };
      if (index != undefined) {
        this.episode.contributions.splice(index + 1, 0, contribution);
      } else {
        this.episode.contributions.push(contribution);
      }
    },

    contributionBtnStyle(contributions, type) {
      let flag = contributions.find(function(contribution) {
        return contribution.type == type;
      });

      return flag ? "btn btn-primary px-4" : "btn btn-light px-4";
    },

    toggleContribution(member_id, member_name, type) {
      let found = this.episode.contributions.find(function(contribution) {
        return contribution.member_id == member_id && contribution.type == type;
      });

      if (found) {
        let index = this.episode.contributions.indexOf(found);
        this.episode.contributions.splice(index, 1);
      } else {
        let firstRow = this.episode.contributions.find(function(contribution) {
          return contribution.member_id == member_id;
        });

        let firstRowIndex = this.episode.contributions.indexOf(firstRow);

        this.pushContribution(member_id, member_name, type, firstRowIndex);
      }
    },

    onSelectMember(memberObj) {
      this.pushContribution(memberObj.id, memberObj.name, "translate");
    },
  },
};
</script>
