import EventIndex from "@/views/Event/index";
import EventCreate from "@/views/Event/create";
import EventEdit from "@/views/Event/edit";

export default [
  {
    path: "event",
    name: "Event",
    component: EventIndex,
    meta: { noGuard: true },
  },
  {
    path: "event/create",
    name: "EventCreate",
    component: EventCreate,
    meta: { noGuard: true },
  },
  {
    path: "event/:id",
    name: "EventEdit",
    component: EventEdit,
    meta: { noGuard: true },
  },
];
