<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <strong class="card-title">Users</strong>
        </div>

        <div class="card-body">
          <div class="text-right mb-3">
            <router-link :to="{name: 'UserMembers'}">
              <div class="btn btn-primary">Members</div>
            </router-link>
          </div>

          <form @submit.prevent="search">
            <div class="form-group">
              <div class="input-group">
                <div class="input-group-addon">
                  <i class="fa fa-search"></i>
                </div>
                <input v-model="searchQuery" type="text" class="form-control">
              </div>
            </div>
          </form>

          <div v-if="users == 'loading'" class="text-center">
            <div class="spinner-border text-primary"></div>
          </div>
          <table v-else-if="users != null" class="table">
            <thead>
              <tr>
                <th scope="col">ID</th>
                <th scope="col">Email</th>
                <th scope="col">Mobile</th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="user in users" :key="user.id">
                <th scope="row">{{ user.id }}</th>
                <td>{{ user.email }}</td>
                <td>{{ user.mobile }}</td>
                <td>
                  <router-link :to="{name: 'UserEdit', params: {id: user.id}}">
                    <div class="btn btn-warning btn-sm">
                      <i class="fas fa-edit"></i>
                    </div>
                  </router-link>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserService from "@/services/UserService";

export default {
  data: function() {
    return {
      users: null,
      searchQuery: ""
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Users");
  },

  methods: {
    search() {
      this.users = "loading";
      UserService.search({ params: { email: this.searchQuery } }).then(res => {
        this.users = res;
      });
    }
  }
};
</script>
