<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <strong class="card-title">Scheduled episodes</strong>
        </div>
        <div class="card-body">
          <table class="table">
            <tbody>
              <tr v-for="schedule in schedules" :key="schedule.file_path">
                <td>{{ schedule.name }}</td>
                <td>{{ schedule.episode_number}}-р анги</td>
                <td>{{ schedule.timestamp }}</td>
                <td>
                  <div @click="deleteSchedule(schedule)" class="btn btn-danger btn-sm">
                    <i class="fas fa-trash"></i>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EpisodeService from "@/services/EpisodeService";

export default {
  data() {
    return {
      schedules: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Scheduled episodes");

    EpisodeService.getSchedules().then(res => {
      this.schedules = res;
    });
  },

  methods: {
    deleteSchedule(schedule) {
      EpisodeService.deleteSchedule(schedule.file_path).then(response => {
        let index = this.schedules.indexOf(schedule);
        this.schedules.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    }
  }
};
</script>
