<template>
  <div class="row">
    <div class="col-12">
      <div v-if="!highlight" class="text-center">
        <div class="spinner-border text-primary"></div>
      </div>
      <div v-else class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <div>
            <router-link :to="{name: 'Highlight'}">
              <div class="btn btn-link">
                <i class="fas fa-arrow-left"></i>
              </div>
            </router-link>
            <strong class="card-title">{{ highlight.name }}</strong>
          </div>
          <router-link :to="{name: 'HighlightAddItem'}">
            <button type="button" class="btn btn-primary px-4">
              <span class="h4 font-weight-bold">+</span>
            </button>
          </router-link>
        </div>

        <div class="card-body">
          <table class="table">
            <tbody>
              <draggable :list="highlight.items" class="dragArea">
                <tr v-for="(item, index) in highlight.items" :key="item.id">
                  <td>
                    <span>{{ index+1 }}</span>
                  </td>
                  <td>
                    <span>{{ item.title.name }}</span>
                  </td>
                  <td>
                    <div @click="deleteItem(item)" class="btn btn-danger btn-sm">
                      <i class="fas fa-trash"></i>
                    </div>
                  </td>
                </tr>
              </draggable>
            </tbody>
          </table>

          <button @click="onSubmit" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HighlightService from "@/services/HighlightService";
import draggable from "vuedraggable";

export default {
  components: { draggable },

  data() {
    return {
      highlight: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Highlight show");
    this.fetchItem();
  },

  methods: {
    fetchItem() {
      HighlightService.fetch(this.$route.params.id).then(res => {
        this.highlight = res;
      });
    },

    deleteItem(items) {
      HighlightService.items_delete(items.id).then(res => {
        let index = this.highlight.items.indexOf(items);
        this.highlight.items.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    onSubmit() {
      const data = this.prepareObject();
      HighlightService.items_sort({
        itemsObj: data
      }).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    prepareObject() {
      let itemsObj = [];
      for (var i = 0; i < this.highlight.items.length; i++) {
        itemsObj.push({
          id: this.highlight.items[i].id,
          position: i + 1
        });
      }
      return itemsObj;
    }
  }
};
</script>
