<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <strong class="card-title">Genres</strong>
          <router-link :to="{name: 'GenreCreate'}">
            <button type="button" class="btn btn-primary px-4">
              <span class="h4 font-weight-bold">+</span>
            </button>
          </router-link>
        </div>
        <div class="card-body">
          <div v-if="genres == 'loading'" class="text-center">
            <div class="spinner-border text-primary"></div>
          </div>
          <table v-else-if="genres != null" class="table">
            <tbody>
              <tr v-for="(genre, index) in genres" :key="genre.id">
                <th scope="row">{{ index+1 }}</th>
                <td>{{ genre.name }}</td>
                <td>
                  <router-link :to="{name: 'GenreEdit', params: {id: genre.id}}">
                    <div class="btn btn-warning btn-sm mx-3">
                      <i class="fas fa-edit"></i>
                    </div>
                  </router-link>

                  <div @click="deleteGenre(genre)" class="btn btn-danger btn-sm mx-3">
                    <i class="fas fa-trash"></i>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GenreService from "@/services/GenreService";

export default {
  data() {
    return {
      genres: "loading"
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Genres");
    this.getGenres();
  },

  methods: {
    getGenres() {
      GenreService.getGenres().then(res => {
        this.genres = res;
      });
    },

    deleteGenre(genre) {
      GenreService.delete(genre.id).then(res => {
        let index = this.genres.indexOf(genre);
        this.genres.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    }
  }
};
</script>
