import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  getGenres() {
    const response = Api().get(apiPath.genres);
    return response;
  },

  getGenre(id) {
    const response = Api().get(apiPath.genres + "/" + id);
    return response;
  },

  create(data) {
    const response = Api().post(apiPath.genres, data);
    return response;
  },

  update(id, data) {
    const response = Api().patch(apiPath.genres + "/" + id, data);
    return response;
  },

  delete(id) {
    const response = Api().delete(apiPath.genres + "/" + id);
    return response;
  }
};
