<template>
  <div class="login-content">
    <div class="login-logo">
      <h1>Serisu Admin</h1>
    </div>
    <div class="login-form">
      <form @submit.prevent="login">
        <div class="form-group">
          <label>Email</label>
          <input type="text" v-model="email" class="form-control" placeholder="Email">
        </div>
        <div class="form-group">
          <label>Password</label>
          <input type="password" v-model="password" class="form-control" placeholder="Password">
        </div>
        <div class="checkbox">
          <label class="pull-right">
            <a href="#">Forgot Password?</a>
          </label>
        </div>
        <button type="submit" class="btn btn-success btn-flat m-b-30 m-t-30">Нэвтрэх</button>
        <div class="register-link m-t-15 text-center">
          <p>
            <router-link :to="{name: 'Register'}">Бүртгүүлэх</router-link>
          </p>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import Cookie from "js-cookie";
import AuthService from "@/services/AuthService";

export default {
  data() {
    return {
      email: "",
      password: ""
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Нэвтрэх");
  },

  methods: {
    login() {
      let data = {
        email: this.email,
        password: this.password
      };

      AuthService.login(data).then(res => {
        if (this.roleCheck("member", res.user.roles)) {
          Cookie.set("auth_token", res.token, { expires: 14 });
          this.$store.dispatch("setAuthUser", res.user);
          this.$router.push({ name: "Homepage" });
        } else {
          this.$snotify.error("Нэвтрэх эрхгүй");
        }
      });
    }
  }
};
</script>
