<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{ name: 'Title' }">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Edit title</strong>
        </div>

        <div v-if="title" class="card-body">
          <div class="form-group">
            <label class="control-label mb-1">Name</label>
            <p>{{ title.name }}</p>
          </div>
          <div class="form-group">
            <label class="control-label mb-1">Plot</label>
            <p>{{ title.plot }}</p>
          </div>

          <div class="row">
            <div class="col-sm-12 col-lg-4">
              <div class="form-group">
                <label class="control-label mb-1">Total episode</label>
                <p>{{ title.total_ep }}</p>
              </div>
            </div>
            <div class="col-sm-12 col-lg-4">
              <div class="form-group">
                <label class="control-label mb-1">Age rating</label>
                <p>{{ title.age_rating }}</p>
              </div>
            </div>
            <div class="col-sm-12 col-lg-4">
              <div class="form-group">
                <label class="control-label mb-1">Youtube URL ID</label>
                <p>{{ title.pv }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TitleService from "@/services/TitleService";

export default {
  data() {
    return {
      title: null,
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Edit title");
    this.getTitle();
  },

  mounted() {},

  methods: {
    getTitle() {
      TitleService.fetch(this.$route.params.id).then((res) => {
        this.title = res;
        document.dispatchEvent(new Event("x-app-rendered"));
      });
    },

    onSubmit() {
      const data = this.createFormData();
      TitleService.update(this.$route.params.id, data).then((res) => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "Title" });
      });
    },

    createFormData() {
      const data = new FormData();
      data.append("_method", "PATCH");
      data.append("name", this.title.name);
      data.append("plot", this.title.plot);
      data.append("total_ep", this.title.total_ep);
      data.append("age_rating", this.title.age_rating);
      data.append("pv", this.title.pv);
      data.append("has_vods", this.title.has_vods ? 1 : 0);
      data.append("poster", document.getElementById("poster").files[0]);
      data.append("cover", document.getElementById("cover").files[0]);
      return data;
    },
  },
};
</script>
