import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  index(titleId) {
    const response = Api().get(apiPath.episodes, {
      params: { title_id: titleId }
    });
    return response;
  },

  fetch(id, options) {
    const response = Api().get(apiPath.episodes + "/" + id, options);
    return response;
  },

  deleteSource(id) {
    const response = Api().delete(apiPath.sources + "/" + id);
    return response;
  },

  async store(data) {
    const response = await Api().post(apiPath.episodes, data, {
      headers: {
        "Content-Type": "multipart/form-data"
      }
    });
    return await response;
  },

  async update(id, data) {
    const response = await Api().post(apiPath.episodes + "/" + id, data, {
      headers: {
        "Content-Type": "multipart/form-data"
      }
    });
    return await response;
  },

  delete(id) {
    const response = Api().delete(apiPath.episodes + "/" + id);
    return response;
  },

  async postVod(data) {
    const response = await Api().post(apiPath.vods, data);
    return await response;
  },

  getSchedules() {
    const response = Api().get(apiPath.scheduledEpisodes);
    return response;
  },

  postSchedule(data) {
    const response = Api().post(apiPath.scheduledEpisodes, data, {
      headers: {
        "Content-Type": "multipart/form-data"
      }
    });
    return response;
  },

  deleteSchedule(filePath) {
    let path = filePath.substring(filePath.lastIndexOf("/") + 1);
    const response = Api().delete(apiPath.scheduledEpisodes + "/" + path);
    return response;
  }
};
