import TitleIndex from "@/views/Title/index";
import TitleCreate from "@/views/Title/create";
import TitleEdit from "@/views/Title/edit";
import EpisodeIndex from "@/views/Episode/index";
import EpisodeEdit from "@/views/Episode/edit";
import EpisodeCreate from "@/views/Episode/create";

export default [
  {
    path: "title",
    name: "Title",
    component: TitleIndex,
    meta: { noGuard: true },
  },
  {
    path: "title/create",
    name: "TitleCreate",
    component: TitleCreate,
    meta: { noGuard: true },
  },
  {
    path: "title/:id",
    name: "TitleEdit",
    component: TitleEdit,
    meta: { noGuard: true },
  },
];
