<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <strong class="card-title">Highlight</strong>
          <router-link :to="{name: 'HighlightCreate'}">
            <button type="button" class="btn btn-primary px-4">
              <span class="h4 font-weight-bold">+</span>
            </button>
          </router-link>
        </div>

        <div class="card-body">
          <table class="table">
            <tbody>
              <draggable :list="highlights" class="dragArea">
                <tr v-for="(highlight, index) in highlights" :key="highlight.id">
                  <td>
                    <span>{{ index+1 }}</span>
                  </td>
                  <td>
                    <router-link :to="{name: 'HighlightShow', params: {id: highlight.id}}">
                      <span>{{ highlight.name }}</span>
                    </router-link>
                  </td>
                  <td>
                    <div @click="deleteItem(highlight)" class="btn btn-danger btn-sm">
                      <i class="fas fa-trash"></i>
                    </div>
                  </td>
                </tr>
              </draggable>
            </tbody>
          </table>

          <button @click="onSubmit" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HighlightService from "@/services/HighlightService";
import draggable from "vuedraggable";

export default {
  components: { draggable },

  data() {
    return {
      highlights: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Highlight");
    this.getHighlights();
  },

  methods: {
    getHighlights() {
      HighlightService.index().then(res => {
        this.highlights = res;
      });
    },

    deleteItem(highlight) {
      HighlightService.delete(highlight.id).then(res => {
        let index = this.highlights.indexOf(highlight);
        this.highlights.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    onSubmit() {
      const data = this.prepareObject();
      HighlightService.sort({
        highlightsObj: data
      }).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    prepareObject() {
      let highlightsObj = [];
      for (var i = 0; i < this.highlights.length; i++) {
        highlightsObj.push({
          id: this.highlights[i].id,
          position: i + 1
        });
      }
      return highlightsObj;
    }
  }
};
</script>
