<template>
  <div class="card">
    <div class="card-header">
      <button @click="addRow" class="btn btn-primary mr-4 px-4">
        <i class="fas fa-plus"></i>
      </button>
      <span>
        <strong>Линкүүд</strong>
      </span>
    </div>

    <div v-if="isDocReady" class="card-body" style="padding-right: 40px;">
      <div class="row">
        <table v-if="sources.length > 0" class="table">
          <tbody>
            <tr v-for="source in sources" :key="source.id">
              <td>
                <div
                  @click="deleteSource(source)"
                  class="btn btn-danger btn-sm mr-3"
                >
                  <i class="fas fa-trash"></i>
                </div>
              </td>
              <td>
                <span class="mr-2">{{ source.host.name }}</span>
              </td>
              <td>
                <a :href="source.value" target="_blank">
                  <span class="text-muted">{{ source.value }}</span>
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div v-if="newSources.length > 0" class="row mt-3">
        <div
          v-for="(newSource, index) in newSources"
          :key="index"
          class="row w-100"
        >
          <div class="col-4">
            <multiselect
              v-model="newSource.host"
              :options="hosts"
              :showLabels="false"
              label="name"
              key="id"
            ></multiselect>
          </div>
          <div class="col-7">
            <div class="form-group">
              <input
                type="text"
                v-model="newSource.value"
                class="form-control"
              />
            </div>
          </div>
          <div class="col-1">
            <div @click="spliceNewSource(index)" class="btn btn-danger btn-sm">
              <i class="fas fa-trash"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import EpisodeService from "@/services/EpisodeService";

export default {
  props: ["hosts", "sources"],
  components: { Multiselect },

  data() {
    return {
      newSources: [],
    };
  },

  computed: {
    isDocReady() {
      return this.hosts ? true : false;
    },
  },

  methods: {
    deleteSource(source) {
      EpisodeService.deleteSource(source.id).then((res) => {
        this.$emit("onSourceDelete", source);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    addRow() {
      this.newSources.push({
        host: {},
        value: null,
      });
    },

    spliceNewSource(index) {
      this.newSources.splice(index, 1);
    },

    emitNewSources() {
      let arr = this.newSources.map((source) => {
        return {
          host_id: source.host.id,
          value: source.value,
        };
      });

      arr = arr.filter((source) => {
        return source.value != null;
      });
      return arr;
    },
  },
};
</script>
