<template>
  <div v-if="genres" class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Franchise'}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Create franchise</strong>
        </div>
        <div class="card-body">
          <div class="form-group">
            <label class="control-label mb-1">Name</label>
            <input type="text" v-model="name" class="form-control">
          </div>

          <label class="control-label mb-1">Type</label>
          <div class="form-group">
            <div class="form-check form-check-inline">
              <input
                class="form-check-input"
                v-model="type"
                type="radio"
                id="inlineRadio1"
                value="series"
              >
              <label class="form-check-label" for="inlineRadio1">Series</label>
            </div>
            <div class="form-check form-check-inline">
              <input
                class="form-check-input"
                v-model="type"
                type="radio"
                id="inlineRadio2"
                value="movie"
              >
              <label class="form-check-label" for="inlineRadio2">Movie</label>
            </div>
          </div>

          <div class="form-group">
            <label class="control-label mb-1">Genres</label>
            <multiselect
              v-model="franchise_genres"
              :options="genres"
              :multiple="true"
              selectLabel="Enter дарж сонгох"
              label="name"
              value="id"
              trackBy="id"
              :closeOnSelect="false"
            ></multiselect>
          </div>

          <button
            @click="onSubmit()"
            class="btn btn-success btn-block"
            :disabled="!isSubmitReady"
          >Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FranchiseService from "@/services/FranchiseService";
import GenreService from "@/services/GenreService";
import Multiselect from "vue-multiselect";

export default {
  components: { Multiselect },

  data() {
    return {
      franchise_genres: [],

      name: null,
      type: null,
      genres: null
    };
  },

  computed: {
    isSubmitReady() {
      return this.name && this.type && this.genres ? true : false;
    }
  },

  created() {
    this.$store.dispatch("setPageTitle", "Create franchise");
    this.getGenres();
  },

  methods: {
    getGenres() {
      GenreService.getGenres().then(res => {
        this.genres = res;
      });
    },

    onSubmit() {
      let genre_ids = this.franchise_genres.map(genre => genre.id);
      let data = {
        genre_ids: genre_ids,
        name: this.name,
        type: this.type
      };

      FranchiseService.store(data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "Franchise" });
      });
    }
  }
};
</script>
