<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Genre'}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Create genre</strong>
        </div>
        <div class="card-body">
          <form @submit.prevent="onSubmit">
            <div class="card-body">
              <div class="form-group">
                <label class="control-label mb-1">Keyword</label>
                <input v-model="keyword" type="text" class="form-control">
              </div>
              <div class="form-group">
                <label class="control-label mb-1">Name</label>
                <input v-model="name" type="text" class="form-control">
              </div>
            </div>

            <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GenreService from "@/services/GenreService";

export default {
  data() {
    return {
      name: "",
      keyword: ""
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Create genre");
  },

  methods: {
    onSubmit() {
      let data = {
        name: this.name,
        keyword: this.keyword
      };

      GenreService.create(data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.clearData();
      });
    },

    clearData() {
      this.name = "";
      this.keyword = "";
    }
  }
};
</script>

