import Settings from "@/views/Settings";
import Genre from "@/views/Settings/Genre/index.vue";
import GenreEdit from "@/views/Settings/Genre/edit.vue";
import GenreCreate from "@/views/Settings/Genre/create.vue";
import Member from "@/views/Settings/Member/index.vue";
import MemberEdit from "@/views/Settings/Member/edit.vue";
import MemberCreate from "@/views/Settings/Member/create.vue";
import MemberStats from "@/views/Settings/Member/stats.vue";
import Host from "@/views/Settings/Host/index.vue";
import HostEdit from "@/views/Settings/Host/edit.vue";
import HostCreate from "@/views/Settings/Host/create.vue";
import MainHighlight from "@/views/Settings/MainHighlight.vue";

export default [
  {
    path: "settings",
    name: "Settings",
    component: Settings,
    meta: { noGuard: true },
  },
  {
    path: "genre",
    name: "Genre",
    component: Genre,
    meta: { noGuard: true },
  },
  {
    path: "genre/create",
    name: "GenreCreate",
    component: GenreCreate,
    meta: { noGuard: true },
  },
  {
    path: "genre/:id",
    name: "GenreEdit",
    component: GenreEdit,
    meta: { noGuard: true },
  },
  {
    path: "member",
    name: "Member",
    component: Member,
    meta: { noGuard: true },
  },
  {
    path: "member/create",
    name: "MemberCreate",
    component: MemberCreate,
    meta: { noGuard: true },
  },
  {
    path: "member/stats",
    name: "MemberStats",
    component: MemberStats,
    meta: { noGuard: true },
  },
  {
    path: "member/:id",
    name: "MemberEdit",
    component: MemberEdit,
    meta: { noGuard: true },
  },
  {
    path: "host",
    name: "Host",
    component: Host,
    meta: { noGuard: true },
  },
  {
    path: "host/create",
    name: "HostCreate",
    component: HostCreate,
    meta: { noGuard: true },
  },
  {
    path: "host/:id",
    name: "HostEdit",
    component: HostEdit,
    meta: { noGuard: true },
  },
  {
    path: "main-highlight",
    name: "MainHighlight",
    component: MainHighlight,
    meta: { noGuard: true },
  },
];
