<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <strong class="card-title">Member</strong>
          <router-link :to="{name: 'MemberCreate'}">
            <div class="btn btn-primary px-4">
              <span class="h4 font-weight-bold">+</span>
            </div>
          </router-link>
        </div>

        <div class="card-body">
          <div class="text-right mb-3">
            <router-link :to="{name: 'MemberStats'}">
              <div class="btn btn-primary">Statistics</div>
            </router-link>
          </div>

          <div v-if="members == null" class="text-center">
            <div class="spinner-border text-primary"></div>
          </div>
          <table v-else class="table">
            <tbody>
              <tr v-for="(member, index) in members" :key="member.id">
                <td>{{ index + 1}}</td>
                <td>{{member.name}}</td>
                <td>
                  <span
                    :class="[member.status == 'active' ? 'badge-success' : 'badge-warning', 'badge']"
                  >{{ member.status }}</span>
                </td>
                <td>
                  <router-link :to="{name: 'MemberEdit', params: {id: member.id}}">
                    <div class="btn btn-warning btn-sm mx-3">
                      <i class="fas fa-edit"></i>
                    </div>
                  </router-link>
                  <div @click="deleteMember(member)" class="btn btn-danger btn-sm mx-3">
                    <i class="fas fa-trash"></i>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MemberService from "@/services/MemberService";

export default {
  data() {
    return {
      members: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Member");
    this.getMembers();
  },

  methods: {
    getMembers() {
      MemberService.index({
        params: { orderBy: "name" }
      }).then(res => {
        let activeMembers = res.filter(member => member.status == "active");
        let inactiveMembers = res.filter(member => member.status == "inactive");
        this.members = activeMembers.concat(inactiveMembers);
      });
    },

    deleteMember(member) {
      MemberService.delete(member.id).then(res => {
        let index = this.members.indexOf(member);
        this.members.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    }
  }
};
</script>
