<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Title'}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Create title</strong>
        </div>

        <div v-if="franchises" class="card-body">
          <div class="form-group">
            <label class="control-label mb-1">Franchise</label>
            <multiselect
              v-model="franchise"
              :options="franchises"
              selectLabel="Enter дарж сонгох"
              label="name"
              value="id"
              trackBy="id"
            ></multiselect>
          </div>
          <div class="form-group">
            <label class="control-label mb-1">Node label</label>
            <input type="text" v-model="node_label" class="form-control">
          </div>

          <hr>

          <div class="form-group">
            <div class="custom-control custom-switch">
              <input v-model="has_vods" type="checkbox" class="custom-control-input" id="vodSwitch">
              <label class="custom-control-label" for="vodSwitch">Vods: {{ has_vods }}</label>
            </div>
          </div>
          <div class="form-group">
            <label class="control-label mb-1">Name</label>
            <input type="text" v-model="name" class="form-control">
          </div>
          <div class="form-group">
            <label class="control-label mb-1">Plot</label>
            <textarea v-model="plot" class="form-control" rows="3"></textarea>
          </div>

          <div class="row">
            <div class="col-sm-12 col-lg-4">
              <div class="form-group">
                <label class="control-label mb-1">Total episode</label>
                <input type="text" v-model="total_ep" placeholder="Жишээ: 10" class="form-control">
              </div>
            </div>
            <div class="col-sm-12 col-lg-4">
              <div class="form-group">
                <label class="control-label mb-1">Age rating</label>
                <input
                  type="text"
                  v-model="age_rating"
                  placeholder="Жишээ: 18"
                  class="form-control"
                >
              </div>
            </div>
            <div class="col-sm-12 col-lg-4">
              <div class="form-group">
                <label class="control-label mb-1">Youtube URL ID</label>
                <input
                  type="text"
                  v-model="pv"
                  placeholder="Жишээ: dQw4w9WgXcQ"
                  class="form-control"
                >
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-sm-12 col-lg-5">
              <div class="card">
                <div class="card-header">Poster</div>
                <div class="card-body">
                  <input type="file" id="poster" accept="image/*" class="my-2">
                </div>
              </div>
            </div>
            <div class="col-sm-12 col-lg-5 offset-lg-1">
              <div class="card">
                <div class="card-header">Cover</div>
                <div class="card-body">
                  <input type="file" id="cover" accept="image/*" class="my-2">
                </div>
              </div>
            </div>
          </div>

          <button
            @click="onSubmit()"
            class="btn btn-success btn-block"
            :disabled="!isSubmitReady || submitSpinner"
          >
            <div v-if="submitSpinner" class="spinner-border" role="status"></div>
            <span v-else>Хадгалах</span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import FranchiseService from "@/services/FranchiseService";
import TitleService from "@/services/TitleService";

export default {
  components: { Multiselect },

  data() {
    return {
      franchises: null,
      submitSpinner: false,

      franchise: null,
      node_label: "1-р бүлэг",
      has_vods: false,
      name: null,
      plot: null,
      total_ep: null,
      age_rating: null,
      pv: null
    };
  },

  computed: {
    isSubmitReady() {
      return this.franchise &&
        this.node_label &&
        this.name &&
        this.plot &&
        this.total_ep &&
        this.age_rating &&
        this.pv
        ? true
        : false;
    }
  },

  created() {
    this.$store.dispatch("setPageTitle", "Create title");
    this.getFranchises();
  },

  methods: {
    getFranchises() {
      FranchiseService.index().then(res => {
        this.franchises = res;
      });
    },

    onSubmit() {
      this.submitSpinner = true;
      const data = this.createFormData();
      TitleService.store(data)
        .then(res => {
          this.$snotify.success(this.captionize("Alert.success"));
          this.submitSpinner = false;
          this.$router.push({ name: "Title" });
        })
        .catch(err => {
          this.submitSpinner = false;
        });
    },

    createFormData() {
      const data = new FormData();
      data.append("franchise_id", this.franchise.id);
      data.append("node_label", this.node_label);
      data.append("name", this.name);
      data.append("plot", this.plot);
      data.append("total_ep", this.total_ep);
      data.append("age_rating", this.age_rating);
      data.append("pv", this.pv);
      data.append("has_vods", this.has_vods ? 1 : 0);
      data.append("poster", document.getElementById("poster").files[0]);
      data.append("cover", document.getElementById("cover").files[0]);
      return data;
    }
  }
};
</script>
