import EpisodeIndex from "@/views/Episode/index";
import EpisodeCreate from "@/views/Episode/create";
import EpisodeEdit from "@/views/Episode/edit";
import EpisodeSchedule from "@/views/Episode/schedule";

export default [
  {
    path: "title/:id/episode",
    name: "EpisodeIndex",
    component: EpisodeIndex,
    meta: { noGuard: true },
  },
  {
    path: "title/:id/episode/create",
    name: "EpisodeCreate",
    component: EpisodeCreate,
    meta: { noGuard: true },
  },
  {
    path: "title/:id/episode/schedule",
    name: "EpisodeSchedule",
    component: EpisodeSchedule,
    meta: { noGuard: true },
  },
  {
    path: "title/:titleId/episode/:episodeId",
    name: "EpisodeEdit",
    component: EpisodeEdit,
    meta: { noGuard: true },
  },
];
