<template>
  <div class="row">
    <div class="col-12">
      <div class="mb-4">
        <a
          class="btn btn-primary"
          data-toggle="collapse"
          href="#chartData"
          role="button"
          aria-expanded="false"
          >Analytics chart</a
        >
        <div class="chart collapse" id="chartData">
          <line-chart v-if="chartData" :data="chartData"></line-chart>
          <div v-else class="text-center">
            <div class="spinner-border text-primary"></div>
          </div>
        </div>
      </div>

      <div v-if="ongoings" class="card">
        <div class="card-header">
          <strong class="card-title">Ongoing titles</strong>
        </div>
        <div class="card-body">
          <table class="table">
            <tbody>
              <tr v-for="title in ongoings" :key="title.id">
                <td>
                  <router-link
                    :to="{ name: 'EpisodeIndex', params: { id: title.id } }"
                  >
                    <span style="color: black;">{{ title.name }}</span>
                  </router-link>
                </td>
                <td>
                  <router-link
                    v-if="roleCheck('admin')"
                    :to="{ name: 'TitleEdit', params: { id: title.id } }"
                  >
                    <div class="btn btn-warning btn-sm">
                      <i class="fas fa-edit"></i>
                    </div>
                  </router-link>
                </td>
                <td>
                  <router-link
                    :to="{ name: 'EpisodeCreate', params: { id: title.id } }"
                  >
                    <div class="btn btn-primary">
                      <i class="fas fa-plus"></i>
                    </div>
                  </router-link>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { APP_NAME } from "@/constants/env";
import OtherService from "@/services/OtherService";
import TitleService from "@/services/TitleService";

export default {
  data() {
    return {
      chartData: null,
      ongoings: null,
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", APP_NAME);
    this.getChartData();
    this.getOngoings();
  },

  methods: {
    getChartData() {
      OtherService.getVisitorsAndViews().then((res) => {
        this.processChartData(res);
      });
    },

    getOngoings() {
      TitleService.index({
        params: { status: "ongoing" },
      }).then((res) => {
        this.ongoings = res;
      });
    },

    processChartData(rawData) {
      let visitorsData = {};
      let pageviewsData = {};

      rawData.forEach((day) => {
        visitorsData[day.date.date] = day.visitors;
        pageviewsData[day.date.date] = day.pageViews;
      });

      let chartData = [
        { name: "Visitors", data: visitorsData },
        { name: "Pageviews", data: pageviewsData },
      ];

      this.chartData = chartData;
    },
  },
};
</script>
