import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  index(options) {
    const response = Api().get(apiPath.events, options);
    return response;
  },

  store(data) {
    const response = Api().post(apiPath.events, data);
    return response;
  },

  fetch(id) {
    const response = Api().get(apiPath.events + "/" + id);
    return response;
  },

  update(id, data) {
    const response = Api().post(apiPath.events + "/" + id, data);
    return response;
  },

  delete(id) {
    const response = Api().delete(apiPath.events + "/" + id);
    return response;
  }
};
