<template>
  <div>
    <div v-if="has_vods" class="card">
      <div class="card-header">
        <button v-if="vod == null" @click="showVod = true" class="btn btn-primary mr-4 px-4">
          <i class="fas fa-plus"></i>
        </button>
        <span>
          <strong>VOD</strong>
        </span>
      </div>

      <div v-if="vod !=null || showVod" class="card-body">
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <div class="input-group-text" style="width: 50px; text-align: center;">
              <strong>SD</strong>
            </div>
          </div>
          <input v-if="vod != null" v-model="vod.sd_value" type="text" class="form-control">
          <input v-else v-model="sd_value" type="text" class="form-control">
        </div>
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <div class="input-group-text" style="width: 50px; text-align: center;">
              <strong>HD</strong>
            </div>
          </div>
          <input v-if="vod != null" v-model="vod.hd_value" type="text" class="form-control">
          <input v-else v-model="hd_value" type="text" class="form-control">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EpisodeService from "@/services/EpisodeService";

export default {
  props: ["has_vods", "episode_id", "vod"],

  data() {
    return {
      showVod: false,
      sd_value: null,
      hd_value: null
    };
  },

  methods: {
    async submitVod(episode_id) {
      if (this.vod == null && this.areFieldsEmpty()) {
        return await [];
      }

      let sdValue = this.vod == null ? this.sd_value : this.vod.sd_value;
      let hdValue = this.vod == null ? this.hd_value : this.vod.hd_value;

      const response = await EpisodeService.postVod({
        episode_id: episode_id,
        sd_value: sdValue,
        hd_value: hdValue
      });

      return await response;
    },

    areFieldsEmpty() {
      let sdEmpty = this.sd_value == null || this.sd_value == "";
      let hdEmpty = this.hd_value == null || this.hd_value == "";
      return sdEmpty && hdEmpty ? true : false;
    }
  }
};
</script>
