import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  login(data) {
    const response = Api().post(apiPath.auth_login, data);
    return response;
  },

  me() {
    const response = Api().post(apiPath.auth_me, {});
    return response;
  },

  register(data) {
    const response = Api().post(apiPath.users, data);
    return response;
  }
};
