import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  index(options) {
    const response = Api().get(apiPath.franchises, options);
    return response;
  },

  store(data) {
    const response = Api().post(apiPath.franchises, data);
    return response;
  },

  fetch(id) {
    const response = Api().get(apiPath.franchises + "/" + id);
    return response;
  },

  update(id, data) {
    const response = Api().patch(apiPath.franchises + "/" + id, data);
    return response;
  },

  delete(id) {
    const response = Api().delete(apiPath.franchises + "/" + id);
    return response;
  },

  updateNodes(data) {
    const response = Api().post(apiPath.franchises + "/nodes", data);
    return response;
  }
};
