import Alert from "./Alert";

const Captions = {
  Alert: Alert
};

export default key => {
  if (!key) {
    return null;
  }

  let keyArray = key.split(".");

  if (keyArray[0] == "Direct") {
    return keyArray[1];
  }

  let segment = null;
  let chain = Captions;

  for (var i = 0; i <= keyArray.length - 1; i++) {
    segment = keyArray[i];
    chain = chain[segment];
  }

  return chain;
};
