<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Event'}" class="btn btn-link">
            <i class="fas fa-arrow-left"></i>
          </router-link>
          <strong class="card-title">Edit event</strong>
        </div>

        <div v-if="event" class="card-body">
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label class="control-label mb-1">Name</label>
              <input v-model="event.name" type="text" class="form-control">
            </div>
            <div class="form-group">
              <div class="custom-control custom-switch">
                <input
                  v-model="event.status"
                  type="checkbox"
                  class="custom-control-input"
                  id="statusSwitch"
                >
                <label class="custom-control-label" for="statusSwitch">Status: {{ event.status }}</label>
              </div>
            </div>
            <div class="row form-group">
              <div class="col col-md-4">
                <img style="width: 200px;" :src=" $apiRoot + event.thumbnail_lg">
              </div>
              <div class="col-12 col-md-8">
                <input type="file" id="thumbnail" accept="image/*">
              </div>
            </div>
            <div class="form-group">
              <label class="control-label mb-1">URL</label>
              <input v-model="event.fb_url" type="text" class="form-control">
            </div>

            <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EventService from "@/services/EventService";

export default {
  data() {
    return {
      event: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Edit event");
    this.fetch();
  },

  methods: {
    fetch() {
      EventService.fetch(this.$route.params.id).then(res => {
        this.event = res;
      });
    },

    onSubmit() {
      let data = this.createFormData();
      EventService.update(this.event.id, data).then(res => {
        this.$router.push({ name: "Event" });
      });
    },

    createFormData() {
      const data = new FormData();
      data.append("_method", "PATCH");
      data.append("name", this.event.name);
      data.append("fb_url", this.event.fb_url);
      let status = this.event.status ? 1 : 0;
      data.append("status", status);
      if (document.getElementById("thumbnail").files[0]) {
        data.append("thumbnail", document.getElementById("thumbnail").files[0]);
      }
      return data;
    }
  }
};
</script>
