<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <strong class="card-title">Event</strong>
          <router-link :to="{name: 'EventCreate'}">
            <button type="button" class="btn btn-primary px-4">
              <span class="h4 font-weight-bold">+</span>
            </button>
          </router-link>
        </div>

        <div v-if="events" class="card-body">
          <table class="table">
            <tbody>
              <tr v-for="event in events" :key="event.id">
                <td>
                  <img :src="$apiRoot + event.thumbnail_lg" style="width: 100%; max-width: 200px;">
                </td>
                <td>
                  <span>{{ event.name }}</span>
                </td>
                <td>
                  <span
                    :class="[event.status ? 'badge-success' : 'badge-warning', 'badge']"
                  >{{ event.status ? 'active' : 'inactive' }}</span>
                </td>
                <td>
                  <router-link :to="{name: 'EventEdit', params: {id: event.id}}">
                    <div class="btn btn-warning btn-sm mx-3">
                      <i class="fas fa-edit"></i>
                    </div>
                  </router-link>
                  <div @click="deleteEvent(event)" class="btn btn-danger btn-sm mx-3">
                    <i class="fas fa-trash"></i>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EventService from "@/services/EventService";

export default {
  data() {
    return {
      events: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Event");
    this.getEvents();
  },

  methods: {
    getEvents() {
      EventService.index({
        params: { dump: "true" }
      }).then(res => {
        this.events = res;
      });
    },

    deleteEvent(event) {
      EventService.delete(event.id).then(res => {
        let index = this.events.indexOf(event);
        this.events.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    }
  }
};
</script>
