<template>
  <div class="login-content">
    <div class="login-logo">
      <h1>Serisu Admin</h1>
    </div>
    <div class="login-form">
      <form @submit.prevent="onSubmit">
        <div class="form-group">
          <label>Email</label>
          <input type="text" v-model="email" class="form-control" placeholder="Email">
        </div>
        <div class="form-group">
          <label>Password</label>
          <input type="password" v-model="password" class="form-control" placeholder="Password">
        </div>
        <div class="form-group">
          <label>Mobile number</label>
          <input type="text" v-model="mobile" class="form-control" placeholder="Mobile number">
        </div>
        <button type="submit" class="btn btn-success btn-flat m-b-30 m-t-30">Бүртгүүлэх</button>
        <div class="register-link m-t-15 text-center">
          <p>
            <router-link :to="{name: 'Login'}">Нэвтрэх</router-link>
          </p>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import AuthService from "@/services/AuthService";

export default {
  data() {
    return {
      email: "",
      password: "",
      passwordRepeat: "",
      mobile: ""
    };
  },

  computed: {
    isFormValid() {
      return this.email && this.password && this.passwordRepeat && this.mobile
        ? true
        : false;
    }
  },

  created() {
    this.$store.dispatch("setPageTitle", "Бүртгүүлэх");
  },

  methods: {
    onSubmit() {
      let data = {
        email: this.email,
        password: this.password,
        mobile: this.mobile
      };

      AuthService.register(data).then(res => {
        this.$snotify.success("Амжилттай бүртгэгдлээ");
        this.$router.push({ name: "Login" });
      });
    }
  }
};
</script>
