<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Member'}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Create member</strong>
        </div>
        <div class="card-body">
          <form @submit.prevent="onSubmit">
            <div class="card-body">
              <div class="form-group">
                <label class="control-label mb-1">Name</label>
                <input v-model="name" type="text" class="form-control">
              </div>
              <div class="form-group">
                <label class="control-label mb-1">Status</label>
                <select v-model="status" class="form-control">
                  <option value="active">active</option>
                  <option value="inactive">inactive</option>
                </select>
              </div>
            </div>

            <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MemberService from "@/services/MemberService";

export default {
  data() {
    return {
      name: "",
      status: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Create member");
  },

  methods: {
    onSubmit() {
      let data = {
        name: this.name,
        status: this.status
      };

      MemberService.create(data).then(res => {
        this.clearData();
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    clearData() {
      this.name = "";
      this.status = null;
    }
  }
};
</script>
