import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  index() {
    const response = Api().get(apiPath.hosts);
    return response;
  },

  fetch(id) {
    const response = Api().get(apiPath.hosts + "/" + id);
    return response;
  },

  create(data) {
    const response = Api().post(apiPath.hosts, data);
    return response;
  },

  update(id, data) {
    const response = Api().patch(apiPath.hosts + "/" + id, data);
    return response;
  },

  delete(id) {
    const response = Api().delete(apiPath.hosts + "/" + id);
    return response;
  }
};
