<template>
  <div class="row">
    <div class="col-12">
      <div v-if="isDocReady" class="card">
        <div class="card-header">
          <router-link :to="{name: 'EpisodeIndex', params: {id: $route.params.id}}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Schedule episode</strong>
        </div>

        <div class="card-body">
          <div class="form-group">
            <label class="control-label mb-1">Ангийн дугаар</label>
            <input v-model="episodeNumber" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label class="control-label mb-1">Schedule date</label>
            <input v-model="scheduleDatetime" type="datetime-local" class="form-control">
          </div>

          <source-manager :sources="[]" ref="sourceManager"></source-manager>

          <vod-manager :has_vods="title.has_vods" :episode_id="null" :vod="null" ref="vodManager"/>

          <div class="row">
            <div class="col-sm-12 col-lg-6">
              <div class="card">
                <div class="card-header">
                  <strong>
                    <span>Thumbnail</span>
                  </strong>
                </div>
                <div class="card-body">
                  <input type="file" id="thumbnail" accept="image/*" class="my-2">
                </div>
              </div>
            </div>
            <div class="col-sm-12 col-lg-6">
              <contribution-manager :episode="episode"></contribution-manager>
            </div>
          </div>

          <button @click="onSubmit" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EpisodeService from "@/services/EpisodeService";
import TitleService from "@/services/TitleService";

import SourceManager from "@/components/misc/Episode/Sources.vue";
import ContributionManager from "@/components/misc/Episode/Contributions.vue";
import VodManager from "@/components/misc/Episode/Vods.vue";

export default {
  components: { SourceManager, ContributionManager, VodManager },

  data() {
    return {
      title: null,
      episodeNumber: null,
      scheduleDatetime: null,
      episode: { contributions: [] }
    };
  },

  computed: {
    isDocReady() {
      return this.title ? true : false;
    }
  },

  created() {
    this.$store.dispatch("setPageTitle", "Schedule episode");
    this.getTitle();
  },

  methods: {
    getTitle() {
      TitleService.fetch(this.$route.params.id)
        .then(res => {
          this.title = res;
        })
        .catch(error => {
          this.$snotify.error(error.message);
        });
    },

    onSubmit() {
      let newSources = this.$refs.sourceManager.emitNewSources();
      const data = this.createFormData(newSources);
      EpisodeService.postSchedule(data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({
          name: "EpisodeIndex",
          params: { id: this.title.id }
        });
      });
    },

    createFormData(newSources) {
      let contributions = this.episode.contributions.map(contribution => {
        return {
          id: contribution.id,
          member_id: contribution.member_id,
          type: contribution.type
        };
      });

      const data = new FormData();
      data.append("title_id", this.$route.params.id);
      data.append("title_name", this.title.name);
      data.append("schedule_datetime", this.scheduleDatetime);
      data.append("episode_number", this.episodeNumber);
      data.append("sources", JSON.stringify(newSources));
      if (contributions.length > 0) {
        data.append("contributions", JSON.stringify(contributions));
      }
      if (document.getElementById("thumbnail").files[0]) {
        data.append("thumbnail", document.getElementById("thumbnail").files[0]);
      }
      return data;
    }
  }
};
</script>
