import Homepage from "@/views/Homepage.vue";
import Report from "@/views/Report.vue";
import Schedules from "@/views/Schedules.vue";

import UserRoute from "./modules/User";
import SettingsRoute from "./modules/Settings";
import EventRoutes from "./modules/Event";
import TrendingRoutes from "./modules/Trending";
import HighlightRoutes from "./modules/Highlight";
import FranchiseRoutes from "./modules/Franchise";
import TitleRoutes from "./modules/Title";
import EpisodeRoutes from "./modules/Episode";

let routes = [];
routes.push(
  {
    path: "",
    name: "Homepage",
    component: Homepage,
    meta: { noGuard: true },
  },
  {
    path: "report",
    name: "Report",
    component: Report,
    meta: { noGuard: true },
  },
  {
    path: "schedules",
    name: "Schedules",
    component: Schedules,
    meta: { noGuard: true },
  }
);

const defaultRoutes = routes.concat(
  UserRoute,
  SettingsRoute,
  EventRoutes,
  TrendingRoutes,
  HighlightRoutes,
  FranchiseRoutes,
  TitleRoutes,
  EpisodeRoutes
);

export default defaultRoutes;
