<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <div>
            <router-link :to="{name: 'UserIndex'}">
              <div class="btn btn-link">
                <i class="fas fa-arrow-left"></i>
              </div>
            </router-link>
            <strong class="card-title">User edit</strong>
          </div>
        </div>
        <div v-if="isDocReady" class="card-body">
          <div class="text-center m-3">
            <h4>{{ user.email }}</h4>
          </div>
          <multiselect
            v-model="userRoles"
            :options="roles"
            :multiple="true"
            selectLabel="Enter дарж сонгох"
            label="name"
            value="id"
            trackBy="id"
          ></multiselect>

          <div @click="onSubmit" class="btn btn-success btn-block mt-3">Хадгалах</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserService from "@/services/UserService";
import Multiselect from "vue-multiselect";

export default {
  components: { Multiselect },

  data() {
    return {
      user: null,
      roles: null,
      userRoles: []
    };
  },

  computed: {
    isDocReady() {
      return this.user && this.roles ? true : false;
    }
  },

  created() {
    this.$store.dispatch("setPageTitle", "User edit");
    this.getUser();
    this.getRoles();
  },

  methods: {
    getRoles() {
      UserService.getRoles().then(res => {
        this.roles = res;
      });
    },

    getUser() {
      UserService.getUser({
        params: {
          id: this.$route.params.id,
          roles: true
        }
      }).then(res => {
        this.user = res;
        res.roles.forEach(role => {
          this.userRoles.push(role);
        });
      });
    },

    onSubmit() {
      let userRoles = this.userRoles.map(role => role.id);
      let data = {
        user_id: this.user.id,
        user_roles: userRoles
      };
      UserService.postRoles(data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "UserIndex" });
      });
    }
  }
};
</script>
