<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <strong class="card-title">Trending</strong>
          <router-link :to="{name: 'TrendingCreate'}">
            <button type="button" class="btn btn-primary px-4">
              <span class="h4 font-weight-bold">+</span>
            </button>
          </router-link>
        </div>

        <div class="card-body">
          <table class="table">
            <tbody>
              <draggable :list="trendings" class="dragArea">
                <tr v-for="(trending, index) in trendings" :key="trending.id">
                  <td>
                    <span>{{ index+1 }}</span>
                  </td>
                  <td>
                    <span>{{ trending.title.name }}</span>
                  </td>
                  <td>
                    <div @click="deleteItem(trending)" class="btn btn-danger btn-sm">
                      <i class="fas fa-trash"></i>
                    </div>
                  </td>
                </tr>
              </draggable>
            </tbody>
          </table>

          <button @click="onSubmit" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TrendingService from "@/services/TrendingService";
import draggable from "vuedraggable";

export default {
  components: {
    draggable
  },

  data() {
    return {
      trendings: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Trending");

    TrendingService.index().then(res => {
      this.trendings = res;
    });
  },

  methods: {
    deleteItem(trending) {
      TrendingService.delete(trending.id).then(res => {
        let index = this.trendings.indexOf(trending);
        this.trendings.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    onSubmit() {
      const data = this.prepareObject();
      TrendingService.sort({
        trendingsObj: data
      }).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
      });
    },

    prepareObject() {
      let trendingsObj = [];
      for (var i = 0; i < this.trendings.length; i++) {
        trendingsObj.push({
          id: this.trendings[i].id,
          position: i + 1
        });
      }
      return trendingsObj;
    }
  }
};
</script>
