<template>
  <div class="row">
    <div class="col-12">
      <div v-if="!isDocReady" class="text-center my-3">
        <div class="spinner-border text-primary"></div>
      </div>

      <div v-else class="card">
        <div
          class="card-header d-flex justify-content-between align-items-center"
        >
          <div class="d-flex align-items-center">
            <router-link
              :to="{
                name: 'EpisodeIndex',
                params: { id: $route.params.titleId },
              }"
            >
              <div class="btn btn-link">
                <i class="fas fa-arrow-left"></i>
              </div>
            </router-link>
            <span>
              <router-link
                :to="{ name: 'TitleEdit', params: { id: title.id } }"
                style="text-decoration: underline; color: black;"
              >
                <strong>{{ title.name }}</strong>
              </router-link>
              : {{ episode.number }}-р анги
            </span>
          </div>
          <div
            @click="deleteEpisode($route.params.episodeId)"
            class="btn btn-danger btn-sm mx-3 px-4"
          >
            <i class="fas fa-trash"></i>
          </div>
        </div>

        <div class="card-body">
          <!-- Sources -->
          <source-manager
            :hosts="hosts"
            :sources="episode.sources"
            ref="sourceManager"
            @onSourceDelete="spliceSource"
          ></source-manager>

          <!-- Contributions -->
          <contribution-manager :members="members" :episode="episode" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import EpisodeService from "@/services/EpisodeService";
import TitleService from "@/services/TitleService";
import HostService from "@/services/HostService";
import MemberService from "@/services/MemberService";

import SourceManager from "@/components/misc/Episode/Sources.vue";
import ContributionManager from "@/components/misc/Episode/Contributions.vue";
import VodManager from "@/components/misc/Episode/Vods.vue";

export default {
  components: { SourceManager, ContributionManager, VodManager },

  data() {
    return {
      title: null,
      episode: null,
      hosts: null,
      members: null,
    };
  },

  computed: {
    isDocReady() {
      return this.title && this.episode ? true : false;
    },
  },

  async mounted() {
    this.$store.dispatch("setPageTitle", "Edit episode");
    await this.getTitle();
    await this.getEpisode();
    await this.getHosts();
    await this.getMembers();
    document.dispatchEvent(new Event("x-app-rendered"));
  },

  methods: {
    async getTitle() {
      let res = await TitleService.fetch(this.$route.params.titleId);
      this.title = res;
    },

    async getEpisode() {
      let res = await EpisodeService.fetch(this.$route.params.episodeId, {
        params: {
          with: ["contributions.member", "sources.host", "vod"],
        },
      });
      this.episode = res;
    },

    async getHosts() {
      let res = await HostService.index();
      this.hosts = res;
    },

    async getMembers() {
      let res = await MemberService.index({
        params: {
          status: "active",
          orderBy: "name",
        },
      });
      this.members = res;
    },

    async onSubmit() {
      let newSources = this.$refs.sourceManager.emitNewSources();
      const data = this.createFormData(newSources);

      // Update Episode
      const episodeRes = await EpisodeService.update(
        this.$route.params.episodeId,
        data
      );

      // Update Vod
      let vodRes;
      if (this.title.has_vods) {
        vodRes = await this.$refs.vodManager.submitVod(this.episode.id);
      }

      this.$snotify.success(this.captionize("Alert.success"));

      this.$router.push({
        name: "EpisodeIndex",
        params: { id: this.title.id },
      });
    },

    spliceSource(source) {
      let index = this.episode.sources.indexOf(source);
      this.episode.sources.splice(index, 1);
    },

    createFormData(newSources) {
      let contributions = this.episode.contributions.map((contribution) => {
        return {
          id: contribution.id,
          member_id: contribution.member_id,
          type: contribution.type,
        };
      });
      const data = new FormData();
      data.append("_method", "PATCH");
      data.append("new_sources", JSON.stringify(newSources));
      data.append("contributions", JSON.stringify(contributions));
      if (document.getElementById("thumbnail").files[0]) {
        data.append("thumbnail", document.getElementById("thumbnail").files[0]);
      }
      return data;
    },

    deleteEpisode(id) {
      EpisodeService.delete(id).then((response) => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({
          name: "EpisodeIndex",
          params: { id: this.title.id },
        });
      });
    },

    clearVod() {},
  },
};
</script>
