<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <strong class="card-title">Report</strong>
        </div>
        <div class="card-body">
          <table class="table">
            <thead>
              <th>Нийт тоо</th>
              <th>Title name</th>
              <th>Episode #</th>
              <th>Watch link</th>
              <th>Delete</th>
            </thead>
            <tbody>
              <tr v-for="report in reports" :key="report.id">
                <td>
                  <span class="text-muted">{{ report.count }}</span>
                </td>
                <td>
                  <router-link
                    :to="{name: 'EpisodeEdit', params: {titleId: report.episode.title.id, episodeId: report.episode.id}}"
                  >
                    <span style="color: black;">{{ report.episode.title.name }}</span>
                  </router-link>
                </td>
                <td>{{ report.episode.number }}</td>
                <td>
                  <a :href="$appUrl + 'watch/' + report.episode.id" target="_blank">
                    <div class="btn btn-link">
                      <i class="fas fa-link"></i>
                    </div>
                  </a>
                </td>
                <td>
                  <div @click="deleteReport(report)" class="btn btn-danger btn-sm">
                    <i class="fas fa-trash"></i>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ReportService from "@/services/ReportService";

export default {
  data() {
    return {
      reports: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Report"), this.getReports();
  },

  methods: {
    getReports() {
      ReportService.index().then(res => {
        this.reports = res;
      });
    },

    deleteReport(report) {
      ReportService.delete(report.id).then(res => {
        let index = this.reports.indexOf(report);
        this.reports.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    }
  }
};
</script>
