import UserIndex from "@/views/User/index";
import UserEdit from "@/views/User/edit";
import UserMembers from "@/views/User/members";

export default [
  {
    path: "user",
    name: "UserIndex",
    component: UserIndex,
    meta: { noGuard: true },
  },
  {
    path: "user/members",
    name: "UserMembers",
    component: UserMembers,
    meta: { noGuard: true },
  },
  {
    path: "user/:id",
    name: "UserEdit",
    component: UserEdit,
    meta: { noGuard: true },
  },
];
