<template>
  <div class="row">
    <div class="col-12">
      <div v-if="host" class="card">
        <div class="card-header">
          <router-link :to="{name: 'Host'}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Edit host</strong>
        </div>
        <div class="card-body">
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label class="control-label mb-1">Keyword</label>
              <input v-model="host.keyword" type="text" class="form-control">
            </div>
            <div class="form-group">
              <label class="control-label mb-1">Name</label>
              <input v-model="host.name" type="text" class="form-control">
            </div>
            <div class="form-group">
              <label class="control-label mb-1">Type</label>
              <select v-model="host.type" class="form-control">
                <option v-for="type in types" :key="type">{{type}}</option>
              </select>
            </div>
            <div class="form-group">
              <label class="control-label mb-1">Front code</label>
              <input v-model="host.front" type="text" class="form-control">
            </div>
            <div class="form-group">
              <label class="control-label mb-1">Back code</label>
              <input v-model="host.back" type="text" class="form-control">
            </div>

            <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HostService from "@/services/HostService";

export default {
  data() {
    return {
      host: null,
      types: ["stream", "download"]
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Edit host");
    this.fetchItem();
  },

  methods: {
    fetchItem() {
      HostService.fetch(this.$route.params.id).then(res => {
        this.host = res;
      });
    },

    onSubmit() {
      let data = {
        keyword: this.host.keyword,
        name: this.host.name,
        type: this.host.type,
        front: this.host.front,
        back: this.host.back
      };

      HostService.update(this.host.id, data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "Host" });
      });
    }
  }
};
</script>
