import Api from "./Api";
import apiPath from "@/constants/apiPath.js";

export default {
  index(options) {
    const response = Api().get(apiPath.titles, options);
    return response;
  },

  fetch(id) {
    const response = Api().get(apiPath.titles + "/" + id);
    return response;
  },

  store(data) {
    const response = Api().post(apiPath.titles, data, {
      headers: {
        "Content-Type": "multipart/form-data"
      }
    });
    return response;
  },

  update(id, data) {
    const response = Api().post(apiPath.titles + "/" + id, data);
    return response;
  },

  delete(id) {
    const response = Api().delete(apiPath.titles + "/" + id);
    return response;
  }
};
