<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'HighlightShow', params: {id: this.$route.params.id}}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Highlight | Add item</strong>
        </div>

        <div class="card-body">
          <div class="form-group">
            <div class="input-group">
              <div class="input-group-addon">
                <i class="fa fa-search"></i>
              </div>
              <input v-model="searchQuery" v-on:input="search" type="text" class="form-control">
            </div>
          </div>

          <div class="form-group form-check">
            <input
              v-model="is_epNumVisible"
              type="checkbox"
              class="form-check-input"
              id="is_epNumVisible"
            >
            <label class="form-check-label" for="is_epNumVisible">Ангийн дугаар үзүүлэх</label>
          </div>

          <div v-if="titles == 'loading'" class="text-center my-3">
            <div class="spinner-border text-primary"></div>
          </div>

          <table v-else class="table">
            <tbody>
              <tr v-for="title in titles" :key="title.id">
                <td>
                  <span>{{ title.name }}</span>
                </td>
                <td>
                  <div
                    @click="selectTitle(title)"
                    :class="[title_id == title.id ? 'btn-success' : 'btn-primary','btn btn-sm']"
                  >Select</div>
                </td>
              </tr>
            </tbody>
          </table>

          <button @click="onSubmit" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TitleService from "@/services/TitleService";
import HighlightService from "@/services/HighlightService";
import _ from "lodash";

export default {
  data() {
    return {
      searchQuery: "",
      titles: null,
      title_id: null,
      is_epNumVisible: false
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Highlight add item");
  },

  methods: {
    search: _.debounce(function(e) {
      let queryParams =
        this.searchQuery != "" ? { name: this.searchQuery } : null;
      this.titles = "loading";
      this.getTitles(queryParams);
    }, 500),

    getTitles(queryParams) {
      this.titles = "loading";
      TitleService.index({
        params: queryParams
      }).then(res => {
        this.titles = res;
      });
    },

    selectTitle(title) {
      this.title_id = title.id;
    },

    onSubmit() {
      let data = {
        highlight_id: this.$route.params.id,
        title_id: this.title_id,
        is_epNumVisible: this.is_epNumVisible
      };

      HighlightService.items_store(data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({
          name: "HighlightShow",
          params: { id: this.$route.params.id }
        });
      });
    }
  }
};
</script>
