import Vue from "vue";
import App from "./App.vue";
import router from "./router/";
import { store } from "./store/";

import Auth from "@/helpers/auth";
Vue.mixin(Auth);

/*** Snotify ***/
import Snotify from "vue-snotify";
const snotifyOptions = {
  toast: {
    showProgressBar: false,
    timeout: 3000,
  },
};
Vue.use(Snotify, snotifyOptions);

/*** Chart ***/
import VueChartkick from "vue-chartkick";
import Chart from "chart.js";
Vue.use(VueChartkick, { adapter: Chart });

/*** Mixin ***/
import mixins from "@/mixins";
Vue.mixin(mixins);

/*** Env variables ***/
import { API_ROOT, APP_URL } from "@/constants/env";
Vue.prototype.$apiRoot = API_ROOT;
Vue.prototype.$appUrl = APP_URL;

/*** Navigation guards ***/
router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.memberGuard)) {
    if (Auth.methods.roleCheck("member")) {
      next();
    } else {
      router.push({ name: "Logout" });
    }
  } else if (to.matched.some((record) => record.meta.adminGuard)) {
    if (Auth.methods.roleCheck("admin")) {
      next();
    } else {
      router.push({ name: "Logout" });
    }
  } else if (to.matched.some((record) => record.meta.superadminGuard)) {
    if (Auth.methods.roleCheck("superadmin")) {
      next();
    } else {
      router.push({ name: "Logout" });
    }
  } else if (to.matched.some((record) => record.meta.noGuard)) {
    if (!Auth.methods.isAuth()) {
      next();
    } else {
      router.push({ name: "Homepage" });
    }
  } else {
    next();
  }
});

// Vue.config.productionTip = false;
Vue.config.devtools = true;

const renderVue = function() {
  new Vue({
    router,
    store,
    render: (h) => h(App),
  }).$mount("#app");
};

/*** Inital set user data ***/
import AuthService from "@/services/AuthService";
import Cookie from "js-cookie";

let token = Cookie.get("auth_token");
if (token) {
  AuthService.me().then((res) => {
    store.dispatch("setAuthUser", res.original);
    renderVue();
  });
} else {
  renderVue();
}
