<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header d-flex justify-content-between align-items-center">
          <strong class="card-title">Franchise</strong>
          <router-link :to="{name: 'FranchiseCreate'}">
            <button type="button" class="btn btn-primary px-4">
              <span class="h4 font-weight-bold">+</span>
            </button>
          </router-link>
        </div>

        <div class="card-body">
          <div class="form-group">
            <div class="input-group">
              <div class="input-group-addon">
                <i class="fa fa-search"></i>
              </div>
              <input v-model="searchQuery" v-on:input="search" type="text" class="form-control">
            </div>
          </div>

          <div v-if="franchises == 'loading'" class="text-center">
            <div class="spinner-border text-primary"></div>
          </div>

          <table v-else class="table">
            <tbody>
              <tr v-for="(franchise, index) in franchises" :key="franchise.id">
                <td>{{ index + 1 }}</td>
                <td>
                  <span>{{ franchise.name }}</span>
                </td>
                <td v-if="roleCheck('admin')">
                  <router-link :to="{name: 'FranchiseEdit', params: {id: franchise.id}}">
                    <div class="btn btn-warning btn-sm mx-3">
                      <i class="fas fa-edit"></i>
                    </div>
                  </router-link>
                  <div
                    v-if="roleCheck('superadmin')"
                    @click="deleteItem(franchise)"
                    class="btn btn-danger btn-sm mx-3"
                  >
                    <i class="fas fa-trash"></i>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FranchiseService from "@/services/FranchiseService";
import _ from "lodash";

export default {
  data() {
    return {
      franchises: null,
      searchQuery: ""
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Franchise");
    this.getFranchises({
      orderBy: "id_desc",
      take: 15
    });
  },

  methods: {
    search: _.debounce(function(e) {
      let queryParams =
        this.searchQuery != "" ? { name: this.searchQuery } : null;
      this.getFranchises(queryParams);
    }, 500),

    getFranchises(queryParams) {
      this.franchises = "loading";
      FranchiseService.index({
        params: queryParams
      }).then(res => {
        this.franchises = res;
      });
    },

    deleteItem(franchise) {
      FranchiseService.delete(franchise.id).then(res => {
        let index = this.franchises.indexOf(franchise);
        this.franchises.splice(index, 1);
        this.$snotify.success(this.captionize("Alert.success"));
      });
    }
  }
};
</script>
