<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Trending'}">
            <div class="btn btn-link">
              <div class="i fas fa-arrow-left"></div>
            </div>
          </router-link>
          <strong class="card-title">Create trending</strong>
        </div>

        <div class="card-body">
          <div class="form-group">
            <div class="input-group">
              <div class="input-group-addon">
                <i class="fa fa-search"></i>
              </div>
              <input v-model="searchQuery" v-on:input="search" type="text" class="form-control">
            </div>
          </div>

          <div v-if="titles == 'loading'" class="my-4 text-center">
            <div class="spinner-border text-primary"></div>
          </div>
          <table v-else class="table">
            <tbody>
              <tr v-for="title in titles" :key="title.id">
                <td>
                  <span>{{ title.name }}</span>
                </td>
                <td>
                  <div
                    @click="selectTitle(title)"
                    :class="[title_id == title.id ? 'btn-success' : 'btn-primary','btn btn-sm']"
                  >Select</div>
                </td>
              </tr>
            </tbody>
          </table>

          <button @click="onSubmit" class="btn btn-success btn-block">Хадгалах</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TrendingService from "@/services/TrendingService";
import TitleService from "@/services/TitleService";

export default {
  data() {
    return {
      titles: null,
      searchQuery: "",
      title_id: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Create trending");
  },

  methods: {
    search: _.debounce(function(e) {
      let queryParams =
        this.searchQuery != "" ? { name: this.searchQuery } : null;
      this.titles = "loading";
      this.getTitles(queryParams);
    }, 500),

    getTitles(queryParams) {
      this.titles = "loading";
      TitleService.index({
        params: queryParams
      }).then(res => {
        this.titles = res;
      });
    },

    selectTitle(title) {
      this.title_id = title.id;
    },

    onSubmit() {
      TrendingService.store({
        title_id: this.title_id
      }).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "Trending" });
      });
    }
  }
};
</script>
