<template>
  <div class="row">
    <div class="col-12">
      <div v-if="genre" class="card">
        <div class="card-header">
          <strong class="card-title">Edit genre</strong>
        </div>
        <form @submit.prevent="onSubmit">
          <div class="card-body">
            <div class="form-group">
              <label class="control-label mb-1">Keyword</label>
              <input v-model="genre.keyword" type="text" class="form-control">
            </div>
            <div class="form-group">
              <label class="control-label mb-1">Name</label>
              <input v-model="genre.name" type="text" class="form-control">
            </div>
          </div>

          <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import GenreService from "@/services/GenreService";

export default {
  data() {
    return {
      genre: null
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Edit genre");
    this.getGenre();
  },

  methods: {
    getGenre() {
      GenreService.getGenre(this.$route.params.id).then(res => {
        this.genre = res;
      });
    },

    onSubmit() {
      let data = {
        keyword: this.genre.keyword,
        name: this.genre.name
      };

      GenreService.update(this.genre.id, data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.$router.push({ name: "Genre" });
      });
    }
  }
};
</script>
