<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div
          class="card-header d-flex justify-content-between align-items-center"
        >
          <strong class="card-title">Title</strong>
        </div>

        <div class="card-body">
          <table class="table">
            <tbody>
              <tr v-for="(title, index) in titles" :key="title.id">
                <td>{{ index + 1 }}</td>
                <td>
                  <router-link
                    :to="{ name: 'EpisodeIndex', params: { id: title.id } }"
                  >
                    <strong>{{ title.name }}</strong>
                  </router-link>
                </td>
                <td>
                  <router-link
                    :to="{ name: 'TitleEdit', params: { id: title.id } }"
                  >
                    <div class="btn btn-warning btn-sm mx-3">
                      <i class="fas fa-edit"></i>
                    </div>
                  </router-link>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TitleService from "@/services/TitleService";
import _ from "lodash";

export default {
  data() {
    return {
      titles: null,
      searchQuery: "",
    };
  },

  async mounted() {
    this.$store.dispatch("setPageTitle", "Title");
    await this.getTitles({
      orderBy: "name_asc",
    });
    document.dispatchEvent(new Event("x-app-rendered"));
  },

  methods: {
    search: _.debounce(function(e) {
      let queryParams =
        this.searchQuery != "" ? { name: this.searchQuery } : null;
      this.getTitles(queryParams);
    }, 500),

    async getTitles(queryParams) {
      let res = await TitleService.index({
        params: queryParams,
      });
      this.titles = res;
    },
  },
};
</script>
