<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-header">
          <router-link :to="{name: 'Host'}">
            <div class="btn btn-link">
              <i class="fas fa-arrow-left"></i>
            </div>
          </router-link>
          <strong class="card-title">Host create</strong>
        </div>
        <div class="card-body">
          <form @submit.prevent="onSubmit">
            <div class="card-body">
              <div class="form-group">
                <label class="control-label mb-1">Keyword</label>
                <input v-model="keyword" type="text" class="form-control">
              </div>
              <div class="form-group">
                <label class="control-label mb-1">Name</label>
                <input v-model="name" type="text" class="form-control">
              </div>
              <div class="form-group">
                <label class="control-label mb-1">Type</label>
                <select v-model="type" class="form-control">
                  <option v-for="type in types" :key="type">{{type}}</option>
                </select>
              </div>
              <div class="form-group">
                <label class="control-label mb-1">Front code</label>
                <input v-model="front" type="text" class="form-control">
              </div>
              <div class="form-group">
                <label class="control-label mb-1">Back code</label>
                <input v-model="back" type="text" class="form-control">
              </div>
            </div>

            <button type="submit" class="btn btn-success btn-block">Хадгалах</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HostService from "@/services/HostService";

export default {
  data() {
    return {
      types: ["stream", "download"],
      keyword: "",
      name: "",
      type: null,
      front: "",
      back: ""
    };
  },

  created() {
    this.$store.dispatch("setPageTitle", "Create host");
  },

  methods: {
    onSubmit() {
      let data = {
        keyword: this.keyword,
        name: this.name,
        type: this.type,
        front: this.front,
        back: this.back
      };

      HostService.create(data).then(res => {
        this.$snotify.success(this.captionize("Alert.success"));
        this.clearData();
      });
    },

    clearData() {
      this.keyword = "";
      this.name = "";
      this.type = "";
      this.front = "";
      this.back = "";
    }
  }
};
</script>

